.info {
    position: relative;
    display: flex;
    height: 680px;
    background-color: #001414;

    &::before {
        content: '';
        flex-shrink: 0;
        width: 287px;
        height: 100%;
        background-image: url("./../../img/info-1.png");

        @include tablet {
            display: none;
        }
    }

    &::after {
        content: '';
        flex-shrink: 0;
        width: 287px;
        height: 100%;
        background-image: url("./../../img/info-2.png");

        @include tablet {
            display: none;
        }
    }

    &__content {
        display: flex;
        flex-grow: 1;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        border-right: 1px solid #001414;
        border-left: 1px solid #001414;
        padding: 0 82px;
        height: 100%;
        color: var(--light-color);

        @include tablet {
            padding: 60px;
        }
    }

    &__text {
        max-width: 542px;
    }

    &__title {
        margin: 0;
        font-weight: 400;
        font-size: 22px;
        line-height: 22px;
    }

    &__descr {
        margin: 0;
        font-size: 15px;
        line-height: 24px;
    }

    @include tablet {
        height: auto;
    }
}